const Profile = () => import( /* webpackChunkName : "about" */ '@/view/Profile.vue');
const Content = () => import( /* webpackChunkName : "Content" */ '@/view/Content.vue');
const Review = () => import( /* webpackChunkName : "Review" */ '@/view/Review.vue')
const UserVerify = () => import( /* webpackChunkName : "UserVerify" */ '@/view/UserVerify.vue')
const Member = () => import( /* webpackChunkName : "Member" */ '@/view/Member.vue')
const LogIn = () => import( /* webpackChunkName : "Store" */ '@/view/LogIn.vue')
const Samples = () => import( /* webpackChunkName : "Store" */ '@/view/Samples.vue')

const routes = [
  {

    path: "/:nation?/:store?/:category?/:product?/:email?",
    component: LogIn,
    name: "login",
  },
  {
    path: "/member",
    component: Member,
    name: "member",
    children: [
      {
        path: "userverify",
        component: UserVerify,
        name: "userverify",
      },
      {
        path: "samples",
        component: Samples,
        name: "samples",
      },
      {
        path: "profile",
        component: Profile,
        name: "profile",
        // meta : {authRequired : true},
      },
      {
        path: "content",
        component: Content,
        name: "content",
      },
      {
        path: "review",
        component: Review,
        name: "review",
      },
    ]
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});
