import http from '@/api/http'
import {push_route} from '@/assets/login.js'
// import router from '@/router/router';

export default {
    namespaced: true,
    state: {
        user_profile: {},
        product_type: {},
        profile_status: false,
        store_data : {},
    },
    mutations: {
        set_user_profile(state, profile) {
            state.user_profile = profile
        },
        set_profile_status(state, status) {
            state.profile_status = status
        },
        set_product_type(state, product_type) {
            state.product_type = product_type
        },
        set_process_step( state, data){
            state.user_profile.process_step = data;
        },
        set_store_data( state, data){
            state.store_data = data
        },
        remove_profile(state) {
            state.user_profile = {};
            state.profile_status = false;
            state.product_type = {};
        },
    },
    actions: {
        // from user vuex
        async initprofile({ state, commit, rootState, dispatch }, id) {
            if (rootState.user.user_status == true) {
                await http.getProfile(id.id)
                    .then((resp) => {
                        console.log( resp.data.store)
                        http.setNationStore( resp.data.store)
                        .then((resp)=>{
                            commit('set_store_data', resp.data)
                        })

                        commit('set_user_profile', resp.data)
                        push_route( state.user_profile.process_step)
                    })
                    .catch(() => {
                        dispatch('user/logout', null, {root: true})
                    })
            }
            else {
                commit('remove_profile')
            }
            if (state.user_profile.product_type != null) {
                await http.getProductType(state.user_profile.product_type)
                    .then((resps) => {
                        commit('set_product_type', resps.data)
                        dispatch('content/init_files', {
                            id: rootState.user.user_info.id,
                            type: state.user_profile.product_type,
                        }, { root: true })
                    })
            }
        },
        async setProfile({ state, commit, rootState, dispatch }, { id, data }) {
            if (rootState.user.user_status == true) {
                await http.setProfile(id, data)
                    .then((resp) => {
                        console.log( "Success")
                        commit('set_user_profile', resp.data)
                        push_route( state.user_profile.process_step)

                        if (state.user_profile.product_type != null) {
                            http.getProductType(state.user_profile.product_type)
                                .then((resp) => {
                                    commit('set_product_type', resp.data)
                                    dispatch('content/init_files', {
                                        id: rootState.user.user_info.id,
                                        type: state.user_profile.product_type,
                                    }, { root: true })
                                })
                        }

                    })
            }
            else {
                console.log("Second")
                dispatch('user/logout', null, { root: true })
            }
        },

        // 더이상 사용하지 않음
        async setProcessStep( {commit}, {id, data}){
            await http.setProcessStep(id, data)
            .then((resp) => {
                commit('set_process_step', resp.data.process_step)
            })


        },
    }
}
