import axios from 'axios'

// local with Lightsail DB
export const BASE_URL = 'http://127.0.0.1:8000/'

// Lightsail Instancd with lightsail db
// export const BASE_URL = 'http://13.125.176.228:8000/'



export default {

    /////////////////// Authority ///////////
    search_user: function (userid, user_mail, user_phone) {
        return axios.post(BASE_URL + "userverify/user-info/",
            {
                "userid": userid,
                "user_mail": user_mail,
                "user_phone": user_phone
            })
    },

    // Make a user -----------------------------------------------------
    make_user: function( data){
        return axios.post( BASE_URL + "userverify/", data)

    },

    // Login Page image and text for Store
    setNationStore: function ( store){
        return axios.get( BASE_URL + "store/" + store + "/")
    },
    // Login Page image and text for Store
    getEntrySetup: function ( ){
        return axios.get( BASE_URL + "entry/1/")
    },



    ///////// profile js http ///////
    getProfile: function (id) {
        return axios.get(BASE_URL + "profile/" + id + "/cur-profile/")
    },
 
    setProfile: function (id, data) {
        return axios.patch(BASE_URL + "profile/" + id + "/put-profile/", data)
    },
    setProcessStep(id, data){
        return axios.patch(BASE_URL + "profile/" + id + "/put-step/", data)
    },
    // product type ------------------------------------------------------------------
    getProductType(id) {
        return axios.get(BASE_URL + "producttype/" + id + "/")
    },
    getCategoryContent(id) {
        return axios.get(BASE_URL + "producttype/" + id + "/all-products/")
    },

    
    // files organaze------------------------------------------------------------
    getUserFiles(id, type) {
        return axios.get(BASE_URL + "api/files/" + id + "/" + type + "/")
    },
    patchFile(id, data) {
        return axios.patch(BASE_URL + "file/" + id + "/", data)
    },
    sortFile(data) {
        return axios.patch(BASE_URL + "file/cur-fileorder/", data)
    },
    // content --------------------------------------------------------------------
    getContext(id){
        return axios.get( BASE_URL + "content/" + id + "/cur-context/")
    },
    patchContext( id, data){
        return axios.patch(BASE_URL + "content/" + id + "/cur-content/", data)
    },
    //// page sample ---------------------------------------------------------------------
    getCategory(id){
        return axios.get( BASE_URL + "category/" + id + "/cate-gory/")
    },
    getProductTy(){
        return axios.get( BASE_URL + "producttype/")
    }

}