import http from '@/api/http'
import {set_local_storage, del_local_storage, push_route } from "@/assets/login.js"
// import store from '../store'
import router from '@/router/router';
// import axios from 'axios';


export default {
    /* eslint-disable no-unused-vars */
    namespaced: true,
    state() {
        return {
            user_modal:false,
            user_info : {},
            user_status: false,
            login_alert: false,
            store_info : {}
        }
    },
    getters: {
        isLogin(state) {
            return state.user_info
        }
    },
    mutations: {
        set_user_modal(state, data) {
            state.user_modal = data
        },
        set_user_info(state, data) {
            state.user_info = data
        },
        set_user_status(state, user_status) {
            state.user_status = user_status
        },
        set_login_alert(state, login_alert) {
            state.login_alert = login_alert
        },
        set_user_store(state, data) {
            state.store_info = data
        },
        remove_user_status(state) {
            state.user_modal =false
            state.user_info = {}
            state.user_status = false
            state.store_info = {}
            state.login_alert = false
        },
    },
    actions: {
        async init_user({ state, commit, dispatch }) {
            const user_mail = localStorage.getItem("user_mail");
            const user_phone = localStorage.getItem("user_phone");
            const userid = localStorage.getItem("userid");
            if (
                !/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(
                    user_mail
                )
            ) {
                dispatch('logout')
                return
            }
            await http.search_user(userid, user_mail, user_phone)
                .then((resp) => {
                    commit('set_user_info', resp.data)
                    commit('set_user_status', true)
                    router
                    // router.push({ name: 'profile' })
                    dispatch('profile/initprofile', { id: state.user_info.id }, { root: true })
                })
                .catch(() => {
                    dispatch('logout')
                })
        },

        // get user
        async user_get({ state, commit, dispatch, rootState}, { userid, user_mail, user_phone, nation, store, product_category, product_type, modal, no_create }) {
            await http.search_user(userid, user_mail, user_phone)
                .then((resp) => {
                    set_local_storage( userid, user_mail, user_phone)

                    commit('set_user_info', resp.data)
                    commit('set_user_status', true)
                    dispatch( 'profile/initprofile', { id: state.user_info.id },  {root: true})
                })
                .catch((err) => {
                    dispatch('logout')
                    if (modal == true){
                        commit('set_user_modal', true)
                        return
                    }
                    if (no_create == false){
                    dispatch( 'make_user',{
                        user_mail : user_mail,
                        user_phone : user_phone,
                        nation : nation,
                        store : store,
                        product_category : product_category, 
                        product_type : product_type,
                        process_step : 1,
                        })
                    }
                })
        },
        // create user
        async make_user( { state, commit, dispatch}, { user_mail, user_phone, nation, store, product_category, product_type, process_step}){
            let data = {
                "user_mail" : user_mail,
                "user_phone" : user_phone,
                "nation" : nation,
                "store" : store,
                "product_category" : product_category,
                "product_type" : product_type,
                "process_step" : 1,
            }
            await http.make_user( data)
            .then((resp) =>{
                set_local_storage( resp.data.id, resp.data.user_mail, resp.data.user_phone)
                commit('set_user_info', resp.data)
                commit('set_user_status', true)
                dispatch( 'profile/initprofile', { id: state.user_info.id },  {root: true})
            })
            .catch(()=>{
                dispatch('logout')
            })
        },

        logout( { commit }){
            del_local_storage()
            commit('remove_user_status')
            commit('profile/remove_profile', null, { root: true })
            commit('content/remove_content_data', null, { root: true })
            push_route( 0)
        },
        /* eslint-enable no-unused-vars */
    }
}