import router from '@/router/router.js'

const set_local_storage = function (userid, user_email, user_phone) {
    localStorage.setItem("userid", userid);
    localStorage.setItem("user_mail", user_email);
    localStorage.setItem("user_phone", user_phone);
}

const del_local_storage = function () {
    localStorage.removeItem("user_mail")
    localStorage.removeItem("user_phone")
    localStorage.removeItem("userid")
}

const push_route = function ( step){

    if ( step == 0) { router.push({ name: 'login' })}
    else if ( step ==1) { router.push({ name: 'samples' })} 
    else if ( step ==2) { router.push({ name: 'samples' })} 
    else if ( step == 3 ) { router.push({ name: 'profile' })}
    else if ( step == 4 ) { router.push({ name: 'content' })}
    else if ( step == 5 ) { router.push({ name: 'previw' })}
    else if ( step == 6 ) { router.push({ name: 'final' })}
    else{ router.push({ name: 'login' })}
}

export { set_local_storage, del_local_storage, push_route}
